export interface ISize {
    w: number;
    h: number;
}

export interface IPosition {
    x: number;
    y: number;
}

export interface IObject extends ISize, IPosition {
    image: string;
}

export interface IScreenConfig {
    type: number;
    size: ISize;
    objects: IObject[];
    seatStart: IPosition;
    map: number[][];
    special: string[];
    hc: string[];
    pair: string[][];
    specialSeats: { name: string; data: string[]; }[];
    seatSize: ISize;
    specialSeatConfig: { name: string; className: string; size: ISize }[];
    seatMargin: ISize;
    aisle: {
        small: ISize;
        middle: ISize;
    };
    seatLabelPos: number;
    seatNumberPos: number;
    seatNumberAlign: 'left' | 'right';
    html: string;
    style?: string;
    columnLabel: boolean;
    lineLabel: boolean;
}

export interface ILabel {
    /**
     * ID
     */
    id: number;
    /**
     * 
     */
    w: number;
    /**
     * 
     */
    h: number;
    /**
     * Y
     */
    y: number;
    /**
     * X
     */
    x: number;
    /**
     * 
     */
    label: string;
}

export interface ISeat {
    /**
     * css
     */
    className: string;
    /**
     * 
     */
    w: number;
    /**
     * 
     */
    h: number;
    /**
     * Y
     */
    y: number;
    /**
     * X
     */
    x: number;
    /**
     * 
     */
    label: string;
    /**
     * 
     */
    status: SeatStatus;
    coaInfo: {
        /**
         * 
         */
        section: string;
        /**
         * 
         */
        seatNum: string;
        /**
         * 
         * 000001002003
         */
        spseatKbn: string;
        /**
         * 
         * 
         */
        spseatAdd1: number;
        /**
         * 
         * 
         */
        spseatAdd2: number;
    };
}

/**
 * 
 */
export enum SeatStatus {
    /**
     * 
     */
    Default = 'default',
    /**
     * 
     */
    Active = 'active',
    /**
     * 
     */
    Disabled = 'disabled'
}
